import { errorHandler } from '@backstage/backend-common';
import express from 'express';
import Router from 'express-promise-router';
import { Logger } from 'winston';

export interface RouterOptions {
  logger: Logger;
}

export async function createRouter(
  options: RouterOptions,
): Promise<express.Router> {
  const { logger } = options;

  const router = Router();
  router.use(express.json());

  router.get('/health', (_, response) => {
    logger.info('PONG!');
    response.json({ status: 'ok' });
  });
  router.get('/topo', (_, response) => {
    // TODO: Change to config.progressiveDelivery.topoPath
    //
    // var data = require('json!/data/data.json');
    // response.write(data);
    fetch('file:///data/data.json').then(resp => {
      if (!resp.ok) {
        response.write("")
      } else {
        response.write(resp.json())
      }
    });
    // }).then(data => response.write(data));
  })
  router.use(errorHandler());
  return router;
}

